.Footer-main {
    display: flex;
    flex-direction: column;
    margin-inline: 4%;
    height: 10vh;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

p {
    width: 100%;
    color: rgb(95, 95, 95);
    text-align: center;
    font-size: 2vh;
}

@media (max-width: 800px) {
    p {
        font-size: 3.5vw;
    }
}